.Contact-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    user-select: text;
}

.Left-container {
    flex-grow: 2;
    background: linear-gradient(135deg, #3cdddd, #dd22a1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;

    height: 100vh;
    width: 100%;

}

.Right-container {
    flex-grow: 1;
    width: 100%;
    background: #1B1E25;
    display: flex;
    flex-direction: row;
    text-align: left;
    justify-content: center;
    /*margin-left: 10px;*/
    /*margin-right: 10px*/
}

.Left-header {
    color: white;
    font-size: calc(10px + 6vmin);
    font-family: 'Segoe Print', serif;
    font-weight: bold;
}

.Right-contents-container {
    /*width: 80%;*/
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.Brief-container {
    align-self: center;
}

.Social-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

.Social-item-container {
    width: 140px;
    height: 140px
}



.Social-image {
    width: 80px;
    height: 80px;
}

